import React, { useState, useEffect } from "react";
import Editor from "./Editor";
import useLocalStorage from "../hooks/useLocalStorage";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCompressAlt, faExpandAlt } from "@fortawesome/free-solid-svg-icons";
import { PasteClient } from "pastebin-api";

function App() {
  const client = new PasteClient("J395uly6sopW4z94IKKLpfAOgE49r4KD");
  const url = client.createPaste({
    code: "const something = 'Hello World!'",
    expireDate: "N",
    format: "javascript",
    name: "something.js",
    publicity: 0,
  });

  console.log(url);
  console.log("hello");
  const [html, setHtml] = useLocalStorage("html", "");
  const [css, setCss] = useLocalStorage("css", "");
  const [js, setJs] = useLocalStorage("js", "");
  const [srcDoc, setSrcDoc] = useState("");
  const [open, setOpen] = useState(true);
  const [lang, setLanguage] = useState("xml");

  useEffect(() => {
    const timeout = setTimeout(() => {
      setSrcDoc(`
    <html>
      <body>${html}</body>
      <style>${css}</style>
      <script>${js}</script>
    </html>
  `);
    }, 250);

    return () => clearTimeout(timeout);
  }, [html, css, js]);

  return (
    <>
      <div className="pane top-pane">
        <div className={`file-container ${open ? "" : "collapsed"}`}>
          <div className="editor-title">
            File Explorer
            <button
              type="button"
              className="file-collapse-btn"
              onClick={() => setOpen((prevOpen) => !prevOpen)}
            >
              <FontAwesomeIcon icon={open ? faCompressAlt : faExpandAlt} />
            </button>
          </div>
          <button
            type="button"
            className="html"
            onClick={() => setLanguage("xml")}
          >
            index.html
          </button>
          <button
            type="button"
            className="css"
            onClick={() => setLanguage("css")}
          >
            index.css
          </button>
          <button
            type="button"
            className="js"
            onClick={() => setLanguage("javascript")}
          >
            index.js
          </button>
          <button type="button" className="save-code">
            SAVE CODE
          </button>
        </div>
        <Editor
          language={lang}
          displayName={lang === "xml" ? "HTML" : lang === "css" ? "CSS" : "JS"}
          value={lang === "xml" ? html : lang === "css" ? css : js}
          onChange={lang === "xml" ? setHtml : lang === "css" ? setCss : setJs}
        />
      </div>
      <div className="live-view-title">Live View</div>
      <div className="pane">
        <iframe
          srcDoc={srcDoc}
          title="output"
          sandbox="allow-scripts"
          frameBorder="0"
          width="100%"
          height="100%"
        />
      </div>
    </>
  );
}

export default App;
